import React from 'react';
import { useEffect, useState } from 'react';
import { interval, Subject } from 'rxjs';
import { takeUntil } from 'rxjs/operators';
import styles from './Timer.module.css';
import Button from '../Button/Button'


const Timer = () => {

  const [time, setTime] = useState(0);
  const [status, setStatus] = useState('stop');
  const [stop, setStop] = useState(0);
        
  const visibleTime = new Date(time).toISOString().slice(11, 19);

  useEffect(() => {

    const observer = new Subject();
    const timeInterval = interval(1000);
      timeInterval
      .pipe(takeUntil(observer))
      .subscribe(() => {
        if (status === 'run') {
          setTime(time => time + 1000);
        }
      });
    
    return () => {
      observer.next();
      observer.complete();
    };
  }, [status]);


    const onStartTimer = () => {
        setStatus('run')
    };

    const onStopTimer = () => {
      setStatus('stop');
      setTime(0);
    };

    const onResetTimer = () => {
      setTime(0);
    };

    const onWaitTimer = () => {
      const stopTimer = Date.now();
        if (stopTimer - stop <= 300) {
         setStatus('wait');
        }
    setStop(stopTimer);
    };
    
  
    return (
      <>
        <div className={styles.timer}>{visibleTime}</div>
        <Button
            onStartTimer={onStartTimer}
            onStopTimer={onStopTimer}
            onResetTimer={onResetTimer}
            onWaitTimer={onWaitTimer}
        />
     
      </>
   
  );
};

export default Timer;